import Activity from './Activity'

class Survey extends Activity {
  static collectionPath({ challengeId }) { return `challenges/${challengeId}/surveys` }
  collectionPath() { return `challenges/${this.challengeId}/surveys` }

  constructor(props) {
    super(props)
    Object.assign(this, props)

    if (props.formUrl) {
      this.formId = props.formUrl.match(/\/d\/e\/(.*)\/.*/)[1]
    }
  }

  static getDefaultValues() {
    return new Survey({
      formUrl: '',
      formEditionUrl: '',
      preview: '',
      authorizedTeams: [],
      prefilledPlayerIdField: false,
      actionButtonText: 'Participer'
    })
  }
}

export default Survey
